
.bookclass {
  width: 100%;
  overflow: hidden;
}
.bookclass > h4 {
    padding: .1rem 0;
    color: #555;
    text-indent: .1rem;
}
.bookclass .b-list {
    white-space: nowrap;
    overflow-x: auto;
    overflow-y: hidden;
}
.bookclass .b-list > li {
      text-align: center;
      display: inline-block;
      margin-left: .1rem;
      width: 1rem;
}
.bookclass .b-list > li > p {
        line-height: 1.8;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
}
.bookclass .b-list .sm {
      font-size: .12rem;
}
.bookclass .b-list .book-bg {
      display: block;
      width: 1rem;
      height: 1.4rem;
      background-color: #00ffcc;
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      margin: 0 auto;
}



